import React, { useEffect, useState } from "react";

import MenuList from "../../components/testPages/MenuList";
import LoginPage from "../../components/testPages/LoginPage";
import TopMenu from "../../components/testPages/TopMenu";
import { useParams, useNavigate } from "react-router-dom";
import { Button, Result, message } from "antd";
const Test = () => {
    const liListUrl = ["/", "/test/login/1", "/test/menu/1", "logout"];
    const navigate = useNavigate();
    let localStorageToken = localStorage.getItem("user_token");
    const [login, setLogin] = useState(localStorageToken);
    let routeParam = useParams();
    //设置鼠标右击页面时的选项
    //useEffect(() => {
    //    document.addEventListener("contextmenu", (e) => {
    //        e.preventDefault(); //取消默认的右键菜单内容
    //        document.getElementById("show_card").style.display = "block";
    //        document.getElementById("show_card").style.left = e.x + "px";
    //        document.getElementById("show_card").style.top = e.y + "px";
    //    });
    //    document.addEventListener("click", (e) => {
    //        document.getElementById("show_card").style.display = "none";
    //    });
    //}, []);
    const clickLi = (number) => {
        if (liListUrl[number] === "logout") {
            //退出登录
            localStorage.removeItem("user_token");
            navigate("/test/login/1");
        } else if (liListUrl[number] === "/test/menu/1") {
            //权限验证
            if (!localStorage.getItem("user_token")) {
                message.error("请先登录！！！");
                navigate(`/test/login/1`);
                return;
            } else {
                navigate(liListUrl[number]);
            }
        } else {
            //普通链接
            navigate(liListUrl[number]);
        }
    };
    useEffect(() => {
        setLogin(localStorageToken);
    }, [localStorageToken]);
    return (
        <div className="w-100 h-100 test_page" style={{ overflowY: "scroll" }}>
            <ul className="show_card" id="show_card">
                <li className="card_opstions" onClick={() => clickLi(0)}>
                    首页
                </li>
                <li className="card_opstions" onClick={() => clickLi(1)}>
                    登录界面
                </li>
                <li className="card_opstions" onClick={() => clickLi(2)}>
                    菜单界面
                </li>
                <li className="card_opstions" onClick={() => clickLi(3)}>
                    退出登录
                </li>
            </ul>
            <div className="wrap">
                <div className="top">
                    <TopMenu current={routeParam.page} login={login} />
                </div>
                <div className="center">
                    {routeParam.page === "login" ? (
                        login ? (
                            <Result
                                status="success"
                                title="登录成功"
                                subTitle="现在你可以选择访问其他页面，或者退出登录"
                                extra={[
                                    <Button
                                        type="primary"
                                        key="console"
                                        onClick={() => {
                                            setLogin(false);
                                            localStorage.removeItem("user_token");
                                        }}
                                    >
                                        退出登录
                                    </Button>,
                                    //
                                    <Button
                                        key="buy"
                                        onClick={() => {
                                            if (localStorage.getItem("user_token")) {
                                                window.open(`http://${window.location.host}/test/menu/1`, "_blank");
                                            } else {
                                                message.error("请先登录！！！");
                                                window.location.reload();
                                            }
                                        }}
                                    >
                                        在新的标签页打开菜单
                                    </Button>,
                                ]}
                            />
                        ) : (
                            <div className="w-100 h-100 flex_c_c" style={routeParam.page === "login" && !login ? { position: "relative" } : {}}>
                                {routeParam.page === "login" && !login ? (
                                    <div class="w-100 h-100 video-wrap">
                                        <video
                                            class="video-wrap__video"
                                            poster="https://qq-web.cdn-go.cn/zc.qq.com/f59c7d00/v3/img/img-bg@2x.png"
                                            src="https://qq-web.cdn-go.cn/zc.qq.com/f59c7d00/v3/img/bg-video.mp4"
                                            tabindex="-1"
                                            airplay="allow"
                                            height="100% "
                                            x-webkit-airplay="true"
                                            playsinline="true"
                                            webkit-playsinline="true"
                                            x5-playsinline="true"
                                            x5-video-player-type="h5"
                                            x5-video-player-fullscreen="true"
                                            autoplay=""
                                            muted=""
                                        ></video>
                                    </div>
                                ) : null}
                                <div
                                    className="flex_c_c"
                                    style={{
                                        width: "400px",
                                        padding: "16px",
                                        //border: "1px solid rgba(0,0,0,0.1)",
                                        //opacity: "0.8",
                                        borderRadius: "4px",
                                        fontSize: "14px",
                                        fontWeight: 400,
                                    }}
                                >
                                    <LoginPage setLogin={setLogin} />
                                </div>
                            </div>
                        )
                    ) : null}
                    {routeParam.page === "menu" ? <MenuList /> : null}
                </div>
                {/*
                <div className="bot"></div>*/}
            </div>
        </div>
    );
};

export default Test;
