import React, { useEffect, useState } from "react";
import { Button, Checkbox, Form, Input, message } from "antd";
const LoginPage = ({ setLogin }) => {
    const formData = {
        username: localStorage.getItem("username") ? localStorage.getItem("username") : "",
        password: localStorage.getItem("password") ? localStorage.getItem("password") : "",
    };
    const remember = localStorage.getItem("username") && localStorage.getItem("password");
    useEffect(() => {
        console.log(formData);
    }, [formData]);

    const onFinish = (values) => {
        console.log("Success:", values);
        message.success("登录成功！");
        localStorage.setItem("user_token", true);
        setLogin(true);
        if (values.remember) {
            localStorage.setItem("username", values.username);
            localStorage.setItem("password", values.password);
        } else {
            localStorage.removeItem("username");
            localStorage.removeItem("password");
        }
    };
    const onFinishFailed = (errorInfo) => {
        console.log("Failed:", errorInfo);
    };
    return (
        <Form
            name="basic"
            labelCol={{
                span: 8,
            }}
            wrapperCol={{
                span: 16,
            }}
            initialValues={{
                username: formData.username,
                password: formData.password,
                remember: remember,
            }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            autoComplete="off"
        >
            {/*<Form.Item>*/}
            <div className="welcome">欢迎登录</div>
            {/*</Form.Item>*/}
            <Form.Item
                label="用户名"
                name="username"
                rules={[
                    {
                        required: true,
                        message: "未输入用户名！",
                    },
                ]}
                //initialValue={formData.username}
            >
                <Input style={{ fontSize: "18px", fontWeight: 400, borderRadius: "4px" }} />
            </Form.Item>

            <Form.Item
                label="密码"
                name="password"
                rules={[
                    {
                        required: true,
                        message: "未输入密码!",
                    },
                ]}
                //initialValue={formData.password}
            >
                <Input.Password style={{ fontSize: "18px", fontWeight: 400, borderRadius: "4px" }} />
            </Form.Item>

            <Form.Item
                name="remember"
                valuePropName="checked"
                wrapperCol={{
                    offset: 8,
                    span: 16,
                }}
            >
                <Checkbox>Remember me</Checkbox>
            </Form.Item>

            <Form.Item
                wrapperCol={{
                    offset: 8,
                    span: 16,
                }}
            >
                <Button type="primary" htmlType="submit" style={{ borderRadius: "6px" }}>
                    Submit
                </Button>
            </Form.Item>
        </Form>
    );
};
export default LoginPage;
