import React, { useState } from "react";
import { AppstoreOutlined, MailOutlined, SettingOutlined } from "@ant-design/icons";
import { Menu, message } from "antd";
import { useParams, useNavigate, useLocation } from "react-router-dom";

const items = [
    {
        label: "登录界面",
        key: "login",
        icon: <MailOutlined />,
        step: 0,
    },
    {
        label: "菜单功能",
        key: "menu",
        icon: <AppstoreOutlined />,
        step: 1,
    },
];
const TopMenu = ({ current, login }) => {
    const navigate = useNavigate();

    const onClick = (e) => {
        if (e.key !== "login" && !localStorage.getItem("user_token")) {
            message.error("请先登录！！！");
            navigate(`/test/login/1`);
            return;
        } else {
            navigate(`/test/${e.key}/1`);
        }
    };

    return <Menu onClick={onClick} selectedKeys={[current]} mode="horizontal" items={items} style={{ height: "100%", fontSize: "20px" }} />;
};
export default TopMenu;
