import React from "react";
import { Menu, message } from "antd";
import { useParams, useNavigate, useLocation } from "react-router-dom";
import Option1 from "./menuListPage/Option1";
const getItem = (label, key, icon, children, type) => {
    return {
        key,
        icon,
        children,
        label,
        type,
    };
};
const items = [
    getItem("Navigation One", "sub1", null, [
        getItem("Item 1", "g1", null, [getItem("Option 1", "1"), getItem("Option 2", "2")], "group"),
        getItem("Item 2", "g2", null, [getItem("Option 3", "3"), getItem("Option 4", "4")], "group"),
    ]),
    getItem("Navigation Two", "sub2", null, [
        getItem("Option 5", "5"),
        getItem("Option 6", "6"),
        getItem("Submenu", "sub3", null, [getItem("Option 7", "7"), getItem("Option 8", "8")]),
    ]),
    getItem("Navigation Three", "sub4", null, [
        getItem("Option 9", "9"),
        getItem("Option 10", "10"),
        getItem("Option 11", "11"),
        getItem("Option 12", "12"),
    ]),
];
const MenuList = () => {
    let routeParam = useParams();
    const navigate = useNavigate();
    const location = useLocation();
    const onClick = (e) => {
        if (localStorage.getItem("user_token")) {
            let paths = JSON.parse(JSON.stringify(e.keyPath));
            paths.shift();
            navigate(`/test/${routeParam.page}/${e.key}`, { state: { defaultOpenKeys: paths } });
        } else {
            message.error("请先登录");
            navigate(`/test/login/1`);
        }
    };

    return (
        <div className="middle">
            <div
                className="left"
                style={{
                    overflowY: "scroll",
                }}
            >
                <Menu
                    onClick={onClick}
                    //style={{
                    //    width: 256,
                    //}}
                    defaultSelectedKeys={[routeParam.id]}
                    defaultOpenKeys={location.state && location.state.defaultOpenKeys ? location.state.defaultOpenKeys : ["sub1"]}
                    mode="inline"
                    items={items}
                />
            </div>
            <div className="right flex_c_c" style={{ fontSize: "22px" }}>
                {routeParam.id == 1 ? <Option1 /> : `这是第${routeParam.id}个选项`}
            </div>
        </div>
    );
};
export default MenuList;
