import React, { useState, useRef, useEffect } from "react";
import { ArrowUpOutlined, ArrowLeftOutlined } from "@ant-design/icons";
import { Menu, Button } from "antd";
import { useParams, useNavigate, useLocation } from "react-router-dom";

const Option1 = () => {
    const data = [
        "这是第一条公告信息！！",
        "这是第二条公告信息！！",
        "这是第三条公告信息！！",
        "这是第四条公告信息！！",
        "这是第五条公告信息！！",
        "这是第六条公告信息！！",
    ];
    //let routeParam = useParams();
    //const navigate = useNavigate();
    //const location = useLocation();
    const [noticeText, setNoticeText] = useState(true);
    const [x, setX] = useState(0);

    useEffect(() => {
        setX(0);
        const interval = setInterval(() => {
            setX((x) => x - 1);
        }, 50);

        return () => clearInterval(interval);
    }, [noticeText]);
    return (
        <div className="middle flex_c_s">
            <div
                className=" notice "
                style={{
                    margin: "16px",
                    paddingLeft: "16px",
                    fontSize: "18px",
                    borderRadius: "8px",
                    border: "2px solid rgba(0,0,0,0.2)",
                    width: "50%",
                }}
            >
                <div>最新公告&nbsp;|&nbsp;</div>
                <div style={{ height: "30px", overflow: "scroll", whiteSpace: noticeText ? "nowrap" : "" }}>
                    <div style={{ transform: `translate${noticeText ? "X" : "Y"}(${x}px)` }}>
                        {data.map((item) => (
                            <span>
                                <span>{item}</span>
                                {noticeText ? " " : <br />}
                            </span>
                        ))}
                    </div>
                </div>
                <div onClick={() => setNoticeText((i) => !i)}>{noticeText ? <ArrowUpOutlined /> : <ArrowLeftOutlined />}</div>
            </div>
        </div>
    );
};
export default Option1;
